        setReportData({
          personalData,
          numerology,
          astrology,
          chineseZodiac,
          astrocartography
        })
        
        // Limpar dados tempor√°rios e localStorage
        if (result.data.paymentId) {
          await fetch(`/api/get-report?payment_id=${result.data.paymentId}`, { method: 'DELETE' })
        }
        
